*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: "Blogger Sans";
  // font-family: "Blogger Sans", "Arial", sans-serif;
  min-width: 320px;
  margin: 0;
  font-size: 16px;
  background-color: $color-white;
}

@media (min-width: $tablet-width) {
  body {
    font-size: 28px;
    background-color: $color-white;
  }
}

@media (min-width: $desktop-width) {
  body {
    background-color: $color-white;
  }
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
